import axios from "axios";
import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  DocumentFewInfo,
  FC_GetDocumentFewInfoList,
} from "../../actions";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextInput from "../../components/Inputs/TextInput";
import { APP_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { setAxiosToken } from "../../utils/AxiosToken";
import Alert from "../../components/Alert/Alert";
import { FcDocument } from "react-icons/fc";
import { Link } from "react-router-dom";
import Modal from "../../components/Modal/Modal";
import { DATE, search } from "../../utils/functions";
export const UPLOAD_DOC_MAX_SIZE = 1048576; // 1MB to Bytes
export const UPLOAD_DOC_MAX_SIZE_MB = 2;
export const UPLOAD_DOC_FILE_TYPE_ERROR_MSG =
  "Le système n'accepte que les documents pdf";
export const SUPORT_FILE_TYPES = ["application/pdf"];

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  upload_percentage: number;
  error: {
    target:
      | "files"
      | "names"
      | "institution"
      | "phone"
      | "email"
      | "reference"
      | "main-success"
      | "main-error"
      | null;
    msg: string;
  };
  success: boolean;
  files: any;
  names: string;
  email: string;
  reference_code: string;
  phone: string;
  institution: string;
  document_id: string;
  document_code: string;
  document_references: DocumentFewInfo[];
  loading_reference: boolean;
  open_select_reference: boolean;
  document_search_input: string;
}

class _CreateDocument extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      success: false,
      files: "",
      upload_percentage: 0,
      error: {
        target: null,
        msg: "",
      },
      names: "",
      email: "",
      reference_code: "",
      phone: "",
      institution: "",
      document_id: "",
      document_code: "",
      document_references: [],
      loading_reference: false,
      open_select_reference: false,
      document_search_input: "",
    };
  }

  componentDidMount = () => {
    FC_GetDocumentFewInfoList(
      (status: boolean, data: DocumentFewInfo[], msg: string) => {
        if (status === true) {
          this.setState({
            document_references: data,
          });
        } else {
          this.setState({
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }
      }
    );
  };

  // upload data
  FC_UploadUserDocumentAPI = async () => {
    console.log("FC_UploadUserDocumentAPI");

    this.setState({
      loading: true,
    });
    try {
      var formData = new FormData();
      formData.append("phone", this.state.phone);
      formData.append("email", this.state.email);
      formData.append("names", this.state.names);
      formData.append("reference_code", this.state.reference_code);
      formData.append("institution", this.state.institution);
      // Add file
      formData.append("files", this.state.files[0]);

      if (!this.state.files) return;
      setAxiosToken();
      const res = await axios.post<{
        document_code: string;
        document_id: string;
        file_url: string;
      }>(`${APP_URL}/document`, formData, {
        onUploadProgress: (progressEvent: any) => {
          let number_percentage =
            (+progressEvent?.loaded / +progressEvent.total) * 100;
          this.setState({
            upload_percentage: number_percentage,
          });
        },
      });

      console.log({ res });

      this.setState({
        loading: false,
        success: true,
        document_code: res.data?.document_code,
        document_id: res.data?.document_id,
      });

      this.clearForm();
    } catch (error) {
      console.log({ error });

      this.setState({
        loading: false,
        error: {
          target: "main-error",
          msg: "msg",
        },
      });
    }
  };

  clearForm = () => {
    this.setState({
      upload_percentage: 0,
      files: "",
      loading: false,
    });
  };

  submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    this.setState({
      error: {
        target: null,
        msg: "",
      },
    });

    if (this.state.names === "")
      return this.setState({
        error: {
          target: "names",
          msg: "Les noms sont obligatoires",
        },
      });

    this.FC_UploadUserDocumentAPI();
  };

  onFileChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({
      error: {
        msg: "",
        target: null,
      },
      files: "",
    });

    // check if the file is available
    if (e.target.files === null || e.target.files === undefined) {
      return this.setState({
        error: {
          msg: "Veuillez sélectionner un fichier pdf",
          target: "files",
        },
      });
    }

    const file = e.target.files[0];

    // check the file type
    if (!SUPORT_FILE_TYPES.includes(file.type)) {
      return this.setState({
        error: {
          msg: UPLOAD_DOC_FILE_TYPE_ERROR_MSG,
          target: "files",
        },
      });
    }

    // check if the size is valued
    if (file.size >= UPLOAD_DOC_MAX_SIZE) {
      return this.setState({
        error: {
          msg: `Le document sélectionné est volumineux, La taille maximale du fichier est (${UPLOAD_DOC_MAX_SIZE_MB}MB)`,
          target: "files",
        },
      });
    }

    // Add image
    this.setState({
      error: {
        msg: "",
        target: null,
      },
      files: e.target.files,
    });
  };

  render() {
    let SearchResults: DocumentFewInfo[] = search(
      this.state.document_references,
      this.state.document_search_input,
      {
        document_code: true,
        domain_id: true,
        owner_email: true,
        owner_institute: true,
        owner_name: true,
        owner_phone: true,
        registered_date: true,
      }
    );
    return (
      <>
        <div className="container mx-auto mb">
          {this.state.error.target === "main-error" && (
            <Alert theme="danger">{this.state.error.msg}</Alert>
          )}
          {this.state.error.target === "main-success" && (
            <Alert theme="success">{this.state.error.msg}</Alert>
          )}
        </div>

        <DashboardContainer className=" bg-white shadow">
          <div className="p-2 py-2 w-full">
            {this.state.success === false && (
              <div className="border-b text-center text-blue-400 pb-3">
                <Heading>
                  <section className="flex items-center">
                    <FcDocument className="inline" />{" "}
                    <span className="font-light">
                      Enregistrer un nouveau document
                    </span>
                  </section>
                </Heading>
              </div>
            )}
            <form onSubmit={this.submitHandler} className="pt-3">
              {this.state.loading === true || this.state.success === true ? (
                <div className="p-3">
                  {this.state.success === true ? (
                    <div>
                      <FcDocument className="text-8xl mx-auto" />

                      <Heading
                        theme="primary"
                        size="md"
                        className="text-lg text-center"
                      >
                        Le document a été enregistré avec succès
                      </Heading>
                      <div className="pt-2 text-lg text-center text-gray-600">
                        <div className="border mx-auto inline-block px-4 py-2">
                          Code du document: <br />{" "}
                          <Heading className="text-black text-xl">
                            {this.state.document_code}
                          </Heading>
                        </div>
                        <div className="p-1 mt-3 mx-auto items-center">
                          <Link to={`/dashboard`}>
                            <Button
                              theme="default"
                              size="sm"
                              className="inline-block mx-1"
                            >
                              Aller à la page des documents
                            </Button>
                          </Link>
                          <Button
                            theme="accent-light"
                            size="sm"
                            className="inline-block mx-1"
                            onClick={() =>
                              this.setState({
                                success: false,
                                document_code: "",
                                document_id: "",
                                email: "",
                                error: {
                                  target: null,
                                  msg: "",
                                },
                                files: "",
                                institution: "",
                                loading: false,
                                names: "",
                                phone: "",
                                reference_code: "",
                                upload_percentage: 0,
                              })
                            }
                          >
                            Ajouter un nouveau document
                          </Button>
                        </div>
                      </div>
                    </div>
                  ) : (
                    <div className="flex">
                      <FcDocument className="text-8xl " />
                      <div className="flex-1">
                        <Heading size="md">
                          Téléchargement du document en cours
                        </Heading>
                        <p>
                          Veuillez patienter pendant que le système télécharge
                          le document
                        </p>
                        <div className="mt-1 bg-blue-200 p-1 rounded-full">
                          <div
                            className="p-2 width-transition bg-blue-600 rounded-full"
                            style={{
                              width: `${this.state.upload_percentage}%`,
                            }}
                          ></div>
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              ) : (
                <>
                  <div className="py-2 pb-4 grid md:grid-cols-2 w-full gap-8 ">
                    <div className="">
                      <label
                        htmlFor="form-select-document"
                        className="text-primary-900 dark:text-primary-100 "
                      >
                        Sélectionnez un document
                      </label>
                      <input
                        onChange={this.onFileChange}
                        type="file"
                        name="files"
                        id="form-select-document"
                        className={`input-white w-full inline-block p-1 rounded bg-blue-100  ${
                          this.state.error?.target === "files"
                            ? "border-red-500 border-2"
                            : "border-blue-500 border-2"
                        }`}
                        placeholder="files"
                        disabled={this.state.loading}
                      />
                      {this.state.error.target === "files" && (
                        <span className="text-red-500 text-sm">
                          {this.state.error.msg}
                        </span>
                      )}
                    </div>

                    <div
                      className="cursor-pointer"
                      onClick={() =>
                        this.setState({ open_select_reference: true })
                      }
                    >
                      <TextInput
                        onChange={(e) =>
                          this.setState({
                            reference_code: e.target.value,
                            error: { target: null, msg: "" },
                          })
                        }
                        value={this.state.reference_code}
                        error={
                          this.state.error.target === "reference"
                            ? this.state.error.msg
                            : ""
                        }
                        className="cursor-pointer"
                        label={"Référence du code du document"}
                        placeholder={"0000/00"}
                        name={""}
                        size="sm"
                        readOnly
                      />
                    </div>
                  </div>

                  <div className="border-blue-200 border rounded lg:p-3">
                    <div className="py-2 px-4">
                      <div className="">
                        <TextInput
                          onChange={(e) =>
                            this.setState({
                              names: e.target.value,
                              error: { target: null, msg: "" },
                            })
                          }
                          value={this.state.names}
                          error={
                            this.state.error.target === "names"
                              ? this.state.error.msg
                              : ""
                          }
                          label={"Owner names"}
                          placeholder={""}
                          name={""}
                          size="sm"
                        />
                      </div>
                      <div className="lg:pt-6 grid md:grid-cols-2 lg:grid-cols-3 pt-3 w-full gap-2 lg:gap-8 ">
                        <div className="">
                          <TextInput
                            onChange={(e) =>
                              this.setState({
                                email: e.target.value,
                                error: { target: null, msg: "" },
                              })
                            }
                            value={this.state.email}
                            error={
                              this.state.error.target === "reference"
                                ? this.state.error.msg
                                : ""
                            }
                            label={"Email"}
                            placeholder={"email@gmail.fr"}
                            name={""}
                            size="sm"
                          />
                        </div>
                        <div className="">
                          <TextInput
                            onChange={(e) =>
                              this.setState({
                                phone: e.target.value,
                                error: { target: null, msg: "" },
                              })
                            }
                            value={this.state.phone}
                            error={
                              this.state.error.target === "phone"
                                ? this.state.error.msg
                                : ""
                            }
                            label={"Phone"}
                            placeholder={""}
                            name={""}
                            size="sm"
                          />
                        </div>
                        <div className="">
                          <TextInput
                            onChange={(e) =>
                              this.setState({
                                institution: e.target.value,
                                error: { target: null, msg: "" },
                              })
                            }
                            value={this.state.institution}
                            error={
                              this.state.error.target === "institution"
                                ? this.state.error.msg
                                : ""
                            }
                            label={"Institution"}
                            placeholder={""}
                            name={""}
                            size="sm"
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </>
              )}

              <div className="mt-2 text-right">
                {this.state.success === false && (
                  <Button type="submit" loading={this.state.loading}>
                    Créer un document
                  </Button>
                )}
              </div>
            </form>
          </div>
        </DashboardContainer>

        {this.state.open_select_reference === true && (
          <Modal
            title="Choisissez un document de référence"
            close={() => this.setState({ open_select_reference: false })}
          >
            <div className="p-1 px-2">
              <TextInput
                onChange={(e) =>
                  this.setState({ document_search_input: e.target.value })
                }
                value={this.state.document_search_input}
                error={""}
                label={""}
                placeholder={"Search a document code"}
                name={""}
                size={"sm"}
              />
            </div>
            <div className="p-2"></div>
            <table className="w-full bg-white">
              <thead>
                <tr className="text-left text-sm bg-gray-100">
                  <th className="p-2 border">Code</th>
                  <th className="p-2 border">Le nom du propriétaire</th>
                  <th className="p-2 border">Nom de l'institution</th>
                  <th className="p-2 border">Date</th>
                  <th className="p-2 border"></th>
                </tr>
              </thead>
              <tbody>
                {this.state.document_search_input.length < 3 ? (
                  <tr>
                    <td className="py-8 text-center" colSpan={5}>
                      <h5 className="text-2xl font-bold text-gray-500">
                        Veuillez saisir le code de référence du document
                      </h5>
                      <p className="text-gray-800">
                        Vous pouvez taper le <b>code-document</b>,{" "}
                        <b>nom du propriétaire du document</b>,{" "}
                        <b>e-mail du propriétaire du document</b>
                      </p>
                    </td>
                  </tr>
                ) : SearchResults.length === 0 ? (
                  <tr>
                    <td className="py-8 text-center" colSpan={5}>
                      <h5 className="text-2xl font-bold text-gray-500">
                        Aucun résultat trouvé!
                      </h5>
                      <p className="text-gray-800">
                        Veuillez bien vérifier le code du document ou essayer un
                        autre code de document
                      </p>
                    </td>
                  </tr>
                ) : (
                  SearchResults.map((itm, i) => (
                    <tr key={i} className="group hover:bg-blue-100">
                      <td className="p-2 border">{itm.document_code}</td>
                      <td className="p-2 border">{itm.owner_name}</td>
                      <td className="p-2 border">{itm.owner_institute}</td>
                      <td className="p-2 border">
                        {DATE(itm.registered_date)}
                      </td>
                      <td className="p-1 border w-40 text-right opacity-40 group-hover:opacity-100">
                        <Button
                          size="sm"
                          onClick={() =>
                            this.setState({
                              reference_code: itm.document_code,
                              names:
                                this.state.names === ""
                                  ? itm.owner_name
                                  : this.state.names,
                              email:
                                this.state.email === ""
                                  ? itm.owner_email
                                  : this.state.email,
                              phone:
                                this.state.phone === ""
                                  ? itm.owner_phone
                                  : this.state.phone,
                              open_select_reference: false,
                            })
                          }
                        >
                          Sélectionnez ce document
                        </Button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </Modal>
        )}
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const CreateDocument = connect(mapStateToProps, {})(_CreateDocument);
