import React, { FC, ReactElement } from "react";
import Button from "../Buttons/Button";

type ButtonProps = {
  close?: (event: any) => void | null;
  children: React.ReactNode;
  backdrop?: boolean;
  className?: string;
  title?: string;
  disabled?: boolean;
};

const Modal: FC<ButtonProps> = ({
  close = null,
  children,
  backdrop = true,
  className = "",
  title = "",
  disabled = false,
}): ReactElement => {
  return (
    <>
      {backdrop === true && (
        <div className="fixed animate__animated animate__fadeIn animate__faster z-60 top-0 right-0 left-0 bottom-0 bg-black bg-opacity-70"></div>
      )}
      <div
        className={`fixed ${
          className === "" ? "w-4/6" : className
        } overflow-auto  rounded-lg modal-component animate__animated animate__zoomIn animate__faster z-60 bg-gray-100 top-0 right-0 left-0 bottom-0`}
      >
        <div className="p-3 rounded-t-lg bg-white border-b flex items-center">
          <h2 className="flex-1 font-bold text-lg text-gray-500">{title}</h2>
          {close !== null && (
            <Button
              disabled={disabled}
              theme="danger-light"
              onClick={close}
              size="sm"
            >
              Fermer
            </Button>
          )}
        </div>
        <div className="overflow-auto">{children}</div>
      </div>
    </>
  );
};
export default Modal;
